import type { SkeletonProps as AntDSkeletonProps } from 'antd';
import { Skeleton as AntDSkeleton } from 'antd';

import { DesignSystemAntDConfigProvider } from '../DesignSystemProvider';
import type { DangerouslySetAntdProps, HTMLDataAttributes } from '../types';
import { visuallyHidden } from '../utils';

export interface SkeletonProps
  extends AntDSkeletonProps,
    DangerouslySetAntdProps<AntDSkeletonProps>,
    HTMLDataAttributes {
  label?: React.ReactNode;
}

interface SkeletonInterface extends React.FC<SkeletonProps> {
  Button: typeof AntDSkeleton.Button;
  Image: typeof AntDSkeleton.Image;
  Input: typeof AntDSkeleton.Input;
}

export const Skeleton = /* #__PURE__ */ (() => {
  const Skeleton: SkeletonInterface = ({ dangerouslySetAntdProps, label, ...props }) => {
    return (
      <DesignSystemAntDConfigProvider>
        <AccessibleContainer label={label}>
          <AntDSkeleton {...props} {...dangerouslySetAntdProps} />
        </AccessibleContainer>
      </DesignSystemAntDConfigProvider>
    );
  };

  Skeleton.Button = AntDSkeleton.Button;
  Skeleton.Image = AntDSkeleton.Image;
  Skeleton.Input = AntDSkeleton.Input;

  return Skeleton;
})();

interface AccessibleContainerProps {
  children: React.ReactNode;
  label?: React.ReactNode;
}

function AccessibleContainer({ children, label }: AccessibleContainerProps) {
  if (!label) {
    return <>{children}</>;
  }

  return (
    <div css={{ cursor: 'progress' }}>
      <span css={visuallyHidden}>{label}</span>
      <div aria-hidden>{children}</div>
    </div>
  );
}
